import request from './request.js';
import store from '@/store';

const appkey = store.state.appkey;

/**
 * 
 * @returns
 */
export async function getPosition() {
    return await request.get('/api/meituan/city/getPosition.json', {
        params: {
            appkey
        }
    })
}

